/* Color Legend */
.legend-container {
  display: flex;
  height: 100%;
}
.legend {
  align-self: flex-end;
  bottom: 2rem;
  position: -webkit-sticky;
  position: sticky;
}
.legend-houses ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}
.legend-houses li {
  margin: 3px 0;
}
.legend-houses span {
  display: inline-block;
}
.legend-house-color {
  position: relative;
  top: 1px;
  width: 15px;
  height: 15px;
  margin-right: 7px;
  border-radius: 50%;
}

/* Adjacency matrix legend */
.matrix-sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding-left: 15px;
}
.matrix-legend ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}
.matrix-legend li {
  margin-right: 10px;
  text-align: center;
}
.matrix-legend .legend-color-circle {
  width: 18px;
  height: 18px;
  border-radius: 50%;
}
.matrix-legend .legend-color-label {
  margin-top: 3px;
  font-size: 1.6rem;
}
.matrix-tooltip {
  margin-top: 20px;
  font-size: 2rem;
}
.matrix-tooltip.hidden {
  opacity: 0;
}
.matrix-tooltip-charA,
.matrix-tooltip-charB,
.matrix-tooltip-scenes {
  font-weight: 700;
}

/* Beeswarm legend */
.beeswarm-legend {
  margin-left: 100px;
}
.beeswarm-legend.hidden {
  opacity: 0;
}
.beeswarm-legend .beeswarm-legend-name {
  font-size: 2rem;
  font-weight: 700;
}